	module.exports = {"data":{"postsJson":{"title":"Third Missionary Journey","questionsTitle":"OPENING THE WORD","display":"block","bookName":"사도행전","ref":"18:23-21:16","html":"<p><strong>18</strong><u>23</u>얼마 있다가 떠나 갈라디아와 브루기아 땅을 차례로 다니며 모든 제자를 굳건하게 하니라<br />   <u>24</u>알렉산드리아에서 난 아볼로라 하는 유대인이 에베소에 이르니 이 사람은 언변이 좋고 성경에 능통한 자라 <u>25</u>그가 일찍이 주의 도를 배워 열심으로 예수에 관한 것을 자세히 말하며 가르치나 요한의 세례만 알 따름이라 <u>26</u>그가 회당에서 담대히 말하기 시작하거늘 브리스길라와 아굴라가 듣고 데려다가 하나님의 도를 더 정확하게 풀어 이르더라<br />   <u>27</u>아볼로가 아가야로 건너가고자 함으로 형제들이 그를 격려하며 제자들에게 편지를 써 영접하라 하였더니 그가 가매 은혜로 말미암아 믿은 자들에게 많은 유익을 주니 <u>28</u>이는 성경으로써 예수는 그리스도라고 증언하여 공중 앞에서 힘있게 유대인의 말을 이김이러라<br />   <strong>19</strong><u>1</u>아볼로가 고린도에 있을 때에 바울이 윗지방으로 다녀 에베소에 와서 어떤 제자들을 만나 <u>2</u>이르되</p><h1>바울</h1><h3>너희가 믿을 때에 성령을 받았느냐</h3><p>이르되</p><h1>에베소의 믿는 사람들</h1><h4>아니라 우리는 성령이 계심도 듣지 못하였노라</h4><p><u>3</u>바울이 이르되</p><h1>바울</h1><h3>그러면 너희가 무슨 세례를 받았느냐</h3><p>대답하되</p><h1>에베소의 믿는 사람들</h1><h4>요한의 세례니라</h4><p><u>4</u>바울이 이르되</p><h1>바울</h1><h3>요한이 회개의 세례를 베풀며 백성에게 말하되 내 뒤에 오시는 이를 믿으라 하였으니 이는 곧 예수라</h3><p>하거늘 <u>5</u>그들이 듣고 주 예수의 이름으로 세례를 받으니 <u>6</u>바울이 그들에게 안수하매 성령이 그들에게 임하시므로 방언도 하고 예언도 하니 <u>7</u>모두 열두 사람쯤 되니라<br />   <u>8</u>바울이 회당에 들어가 석 달 동안 담대히 하나님 나라에 관하여 강론하며 권면하되 <u>9</u>어떤 사람들은 마음이 굳어 순종하지 않고 무리 앞에서 이 도를 비방하거늘 바울이 그들을 떠나 제자들을 따로 세우고 두란노 서원에서 날마다 강론하니라 <u>10</u>두 해 동안 이같이 하니 아시아에 사는 자는 유대인이나 헬라인이나 다 주의 말씀을 듣더라<br />   <u>11</u>하나님이 바울의 손으로 놀라운 능력을 행하게 하시니<u>12</u>심지어 사람들이 바울의 몸에서 손수건이나 앞치마를 가져다가 병든 사람에게 얹으면 그 병이 떠나고 악귀도 나가더라<br />   <u>13</u>이에 돌아다니며 마술하는 어떤 유대인들이 시험삼아 악귀 들린 자들에게 주 예수의 이름을 불러 말하되</p><h1>스게와의 일곱 아들들</h1><h4>내가 바울이 전파하는 예수를 의지하여 너희에게 명하노라</h4><p>하더라 <u>14</u>유대의 한 제사장 스게와의 일곱 아들도 이 일을 행하더니<u>15</u>악귀가 대답하여 이르되</p><h1>악귀</h1><h4>내가 예수도 알고 바울도 알거니와 너희는 누구냐</h4><p>하며 <u>16</u>악귀 들린 사람이 그들에게 뛰어올라 눌러 이기니 그들이 상하여 벗은 몸으로 그 집에서 도망하는지라<br />   <u>17</u>에베소에 사는 유대인과 헬라인들이 다 이 일을 알고 두려워하며 주 예수의 이름을 높이고 <u>18</u>믿은 사람들이 많이 와서 자복하여 행한 일을 알리며 <u>19</u>또 마술을 행하던 많은 사람이 그 책을 모아 가지고 와서 모든 사람 앞에서 불사르니 그 책 값을 계산한즉 은 오만이나 되더라 <u>20</u>이와 같이 주의 말씀이 힘이 있어 흥왕하여 세력을 얻으니라<br />   <u>21</u>이 일이 있은 후에 바울이 마게도냐와 아가야를 거쳐 예루살렘에 가기로 작정하여 이르되</p><h1>바울</h1><h3>내가 거기 갔다가 후에 로마도 보아야 하리라</h3><p>하고 <u>22</u>자기를 돕는 사람 중에서 디모데와 에라스도 두 사람을 마게도냐로 보내고 자기는 아시아에 얼마 동안 더 있으니라<br />   <u>23</u>그 때쯤 되어 이 도로 말미암아 적지 않은 소동이 있었으니 <u>24</u>즉 데메드리오라 하는 어떤 은장색이 은으로 아데미의 신상 모형을 만들어 직공들에게 적지 않은 벌이를 하게 하더니 <u>25</u>그가 그 직공들과 그러한 영업하는 자들을 모아 이르되</p><h1>에베소의 데메드리오</h1><h4>여러분도 알거니와 우리의 풍족한 생활이 이 생업에 있는데 <u>26</u>이 바울이 에베소뿐 아니라 거의 전 아시아를 통하여 수많은 사람을 권유하여 말하되 사람의 손으로 만든 것들은 신이 아니라 하니 이는 그대들도 보고 들은 것이라 <u>27</u>우리의 이 영업이 천하여질 위험이 있을 뿐 아니라 큰 여신 아데미의 신전도 무시 당하게 되고 온 아시아와 천하가 위하는 그의 위엄도 떨어질까 하노라</h4><p>하더라<br />   <u>28</u>그들이 이 말을 듣고 분노가 가득하여 외쳐 이르되</p><h1>에베소의 은장색공들</h1><h4>크다 에베소 사람의 아데미여</h4><p>하니 <u>29</u>온 시내가 요란하여 바울과 같이 다니는 마게도냐 사람 가이오와 아리스다고를 붙들어 일제히 연극장으로 달려 들어가는지라 <u>30</u>바울이 백성 가운데로 들어가고자 하나 제자들이 말리고 <u>31</u>또 아시아 관리 중에 바울의 친구된 어떤 이들이 그에게 통지하여 연극장에 들어가지 말라 권하더라<br />   <u>32</u>사람들이 외쳐 어떤 이는 이런 말을, 어떤 이는 저런 말을 하니 모인 무리가 분란하여 태반이나 어찌하여 모였는지 알지 못하더라 <u>33</u>유대인들이 무리 가운데서 알렉산더를 권하여 앞으로 밀어내니 알렉산더가 손짓하며 백성에게 변명하려 하나 <u>34</u>그들은 그가 유대인인 줄 알고 다 한 소리로 외쳐 이르되</p><h1>에베소 사람들</h1><h4>크다 에베소 사람의 아데미여</h4><p>하기를 두 시간이나 하더니 <u>35</u>서기장이 무리를 진정시키고 이르되</p><h1>에베소의 서기장</h1><h4>에베소 사람들아 에베소 시가 큰 아데미와 제우스에게서 내려온 우상의 신전지기가 된 줄을 누가 알지 못하겠느냐 <u>36</u>이 일이 그렇지 않다 할 수 없으니 너희가 가만히 있어서 무엇이든지 경솔히 아니하여야 하리라<br />   <u>37</u>신전의 물건을 도둑질하지도 아니하였고 우리 여신을 비방하지도 아니한 이 사람들을 너희가 붙잡아 왔으니 <u>38</u>만일 데메드리오와 그와 함께 있는 직공들이 누구에게 고발할 것이 있으면 재판 날도 있고 총독들도 있으니 피차 고소할 것이요 <u>39</u>만일 그 외에 무엇을 원하면 정식으로 민회에서 결정할지라 <u>40</u>오늘 아무 까닭도 없는 이 일에 우리가 소요 사건으로 책망 받을 위험이 있고 우리는 이 불법 집회에 관하여 보고할 자료가 없다</h4><p>하고 <u>41</u>이에 그 모임을 흩어지게 하니라<br />   <strong>20</strong><u>1</u>소요가 그치매 바울은 제자들을 불러 권한 후에 작별하고 떠나 마게도냐로 가니라 <u>2</u>그 지방으로 다녀가며 여러 말로 제자들에게 권하고 헬라에 이르러 <u>3</u>거기 석 달 동안 있다가 배 타고 수리아로 가고자 할 그 때에 유대인들이 자기를 해하려고 공모하므로 마게도냐를 거쳐 돌아가기로 작정하니 <u>4</u>아시아까지 함께 가는 자는 베뢰아 사람 부로의 아들 소바더와 데살로니가 사람 아리스다고와 세군도와 더베 사람 가이오와 및 디모데와 아시아 사람 두기고와 드로비모라 <u>5</u>그들은 먼저 가서 드로아에서 우리를 기다리더라 <u>6</u>우리는 무교절 후에 빌립보에서 배로 떠나 닷새 만에 드로아에 있는 그들에게 가서 이레를 머무니라<br />   <u>7</u>그 주간의 첫날에 우리가 떡을 떼려 하여 모였더니 바울이 이튿날 떠나고자 하여 그들에게 강론할새 말을 밤중까지 계속하매 <u>8</u>우리가 모인 윗다락에 등불을 많이 켰는데 <u>9</u>유두고라 하는 청년이 창에 걸터 앉아 있다가 깊이 졸더니 바울이 강론하기를 더 오래 하매 졸음을 이기지 못하여 삼 층에서 떨어지거늘 일으켜보니 죽었는지라 <u>10</u>바울이 내려가서 그 위에 엎드려 그 몸을 안고 말하되</p><h1>바울</h1><h3>떠들지 말라 생명이 그에게 있다</h3><p>하고 <u>11</u>올라가 떡을 떼어 먹고 오랫동안 곧 날이 새기까지 이야기하고 떠나니라 <u>12</u>사람들이 살아난 청년을 데리고 가서 적지 않게 위로를 받았더라<br />   <u>13</u>우리는 앞서 배를 타고 앗소에서 바울을 태우려고 그리로 가니 이는 바울이 걸어서 가고자 하여 그렇게 정하여 준 것이라 <u>14</u>바울이 앗소에서 우리를 만나니 우리가 배에 태우고 미둘레네로 가서 <u>15</u>거기서 떠나 이튿날 기오 앞에 오고 그 이튿날 사모에 들르고 또 그 다음 날 밀레도에 이르니라<br />   <u>16</u>바울이 아시아에서 지체하지 않기 위하여 에베소를 지나 배 타고 가기로 작정하였으니 이는 될 수 있는 대로 오순절 안에 예루살렘에 이르려고 급히 감이러라 <u>17</u>바울이 밀레도에서 사람을 에베소로 보내어 교회 장로들을 청하니 <u>18</u>오매 그들에게 말하되</p><h1>바울</h1><h3>아시아에 들어온 첫날부터 지금까지 내가 항상 여러분 가운데서 어떻게 행하였는지를 여러분도 아는 바니 <u>19</u>곧 모든 겸손과 눈물이며 유대인의 간계로 말미암아 당한 시험을 참고 주를 섬긴 것과 <u>20</u>유익한 것은 무엇이든지 공중 앞에서나 각 집에서나 거리낌이 없이 여러분에게 전하여 가르치고 <u>21</u>유대인과 헬라인들에게 하나님께 대한 회개와 우리 주 예수 그리스도께 대한 믿음을 증언한 것이라<br />   <u>22</u>보라 이제 나는 성령에 매여 예루살렘으로 가는데 거기서 무슨 일을 당할는지 알지 못하노라 <u>23</u>오직 성령이 각 성에서 내게 증언하여 결박과 환난이 나를 기다린다 하시나 <u>24</u>내가 달려갈 길과 주 예수께 받은 사명 곧 하나님의 은혜의 복음을 증언하는 일을 마치려 함에는 나의 생명조차 조금도 귀한 것으로 여기지 아니하노라<br />   <u>25</u>보라 내가 여러분 중에 왕래하며 하나님의 나라를 전파하였으나 이제는 여러분이 다 내 얼굴을 다시 보지 못할 줄 아노라 <u>26</u>그러므로 오늘 여러분에게 증언하거니와 모든 사람의 피에 대하여 내가 깨끗하니<u>27</u>이는 내가 꺼리지 않고 하나님의 뜻을 다 여러분에게 전하였음이라<br />   <u>28</u>여러분은 자기를 위하여 또는 온 양 떼를 위하여 삼가라 성령이 그들 가운데 여러분을 감독자로 삼고 하나님이 자기 피로 사신 교회를 보살피게 하셨느니라 <u>29</u>내가 떠난 후에 사나운 이리가 여러분에게 들어와서 그 양 떼를 아끼지 아니하며<u>30</u>또한 여러분 중에서도 제자들을 끌어 자기를 따르게 하려고 어그러진 말을 하는 사람들이 일어날 줄을 내가 아노라 <u>31</u>그러므로 여러분이 일깨어 내가 삼 년이나 밤낮 쉬지 않고 눈물로 각 사람을 훈계하던 것을 기억하라<br />   <u>32</u>지금 내가 여러분을 주와 및 그 은혜의 말씀에 부탁하노니 그 말씀이 여러분을 능히 든든히 세우사 거룩하게 하심을 입은 모든 자 가운데 기업이 있게 하시리라<br />   <u>33</u>내가 아무의 은이나 금이나 의복을 탐하지 아니하였고 <u>34</u>여러분이 아는 바와 같이 이 손으로 나와 내 동행들이 쓰는 것을 충당하여<u>35</u>범사에 여러분에게 모본을 보여준 바와 같이 수고하여 약한 사람들을 돕고 또 주 예수께서 친히 말씀하신 바 주는 것이 받는 것보다 복이 있다 하심을 기억하여야 할지니라</h3><p><u>36</u>이 말을 한 후 무릎을 꿇고 그 모든 사람들과 함께 기도하니 <u>37</u>다 크게 울며 바울의 목을 안고 입을 맞추고 <u>38</u>다시 그 얼굴을 보지 못하리라 한 말로 말미암아 더욱 근심하고 배에까지 그를 전송하니라<br />   <strong>21</strong><u>1</u>우리가 그들을 작별하고 배를 타고 바로 고스로 가서 이튿날 로도에 이르러 거기서부터 바다라로 가서 <u>2</u>베니게로 건너가는 배를 만나서 타고 가다가 <u>3</u>구브로를 바라보고 이를 왼편에 두고 수리아로 항해하여 두로에서 상륙하니 거기서 배의 짐을 풀려 함이러라<br />   <u>4</u>제자들을 찾아 거기서 이레를 머물더니 그 제자들이 성령의 감동으로 바울더러 예루살렘에 들어가지 말라 하더라 <u>5</u>이 여러 날을 지낸 후 우리가 떠나갈새 그들이 다 그 처자와 함께 성문 밖까지 전송하거늘 우리가 바닷가에서 무릎을 꿇어 기도하고 <u>6</u>서로 작별한 후 우리는 배에 오르고 그들은 집으로 돌아가니라<br />   <u>7</u>두로를 떠나 항해를 다 마치고 돌레마이에 이르러 형제들에게 안부를 묻고 그들과 함께 하루를 있다가 <u>8</u>이튿날 떠나 가이사랴에 이르러 일곱 집사 중 하나인 전도자 빌립의 집에 들어가서 머무르니라 <u>9</u>그에게 딸 넷이 있으니 처녀로 예언하는 자라<br />   <u>10</u>여러 날 머물러 있더니 아가보라 하는 한 선지자가 유대로부터 내려와 <u>11</u>우리에게 와서 바울의 띠를 가져다가 자기 수족을 잡아매고 말하기를</p><h1>돌레미아의 아가보</h1><h4>성령이 말씀하시되예루살렘에서 유대인들이 이같이 이 띠 임자를 결박하여 이방인의 손에 넘겨 주리라</h4><p>하거늘 <u>12</u>우리가 그 말을 듣고 그 곳 사람들과 더불어 바울에게 예루살렘으로 올라가지 말라 권하니<br />   <u>13</u>바울이 대답하되</h1><h1>바울</h1><h3>여러분이 어찌하여 울어 내 마음을 상하게 하느냐 나는 주 예수의 이름을 위하여 결박 당할 뿐 아니라 예루살렘에서 죽을 것도 각오하였노라</h3><p>하니 <u>14</u>그가 권함을 받지 아니하므로</p><h1>가이사랴의 장로들</h1><h4>우리가 주의 뜻대로 이루어지이다</h4><p>하고 그쳤노라<br />   <u>15</u>이 여러 날 후에 여장을 꾸려 예루살렘으로 올라갈새<u>16</u>가이사랴의 몇 제자가 함께 가며 한 오랜 제자 구브로 사람 나손을 데리고 가니 이는 우리가 그의 집에 머물려 함이라</p>","path":"/360","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?","prevPath":"/359","nextPath":"/361"}},"pathContext":{}}

